// --sb-icon__size
// --sb-icon__padding
// --sb-icon__color
// --sb-icon__hover-color

.sb-icon {
  color: var(--sb-icon__color, inherit);
  padding: var(--sb-icon_padding, 2px);

  :hover {
    color: var(--sb-icon__hover-color, inherit);
  }
}

.sb-icon__size-auto {
  font-size: var(--sb-icon__font-size, 16px);
  height: 100%;
  height: var(--sb-icon__size, 100%);
  width: var(--sb-icon__size, 100%);
}

.sb-icon__size-smaller {
  font-size: var(--sb-icon__font-size, var(--sbt-font-size-20));
  height: var(--sb-icon__size, 12px);
  width: var(--sb-icon__size, 12px);
}

.sb-icon__size-small {
  font-size: var(--sb-icon__font-size, var(--sbt-font-size-30));
  height: var(--sb-icon__size, 16px);
  width: var(--sb-icon__size, 16px);
}

.sb-icon__size-medium {
  font-size: var(--sb-icon__font-size, var(--sbt-font-size-40));
  height: var(--sb-icon__size, 20px);
  width: var(--sb-icon__size, 20px);
}

.sb-icon__size-large {
  font-size: var(--sb-icon__font-size, var(--sbt-font-size-70));
  height: var(--sb-icon__size, 32px);
  width: var(--sb-icon__size, 32px);
}

.sb-icon__size-larger {
  font-size: var(--sb-icon__font-size, var(--sbt-font-size-80));
  height: var(--sb-icon__size, 48px);
  width: var(--sb-icon__size, 48px);
}
